import React from "react";
import "../../node_modules/font-awesome/css/font-awesome.min.css";
import styles from "./card.css";

class Body extends React.Component {
  constructor(props) {
    super(props);
    this.state = { page: 1 };
  }

  componentDidMount() {
    // 
    // 
    window.addEventListener("scroll", async () => {
      console.log("scroll");
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      const clientHeight =
        document.documentElement.clientHeight || document.body.clientHeight;
      const scrollHeight =
        document.documentElement.scrollHeight || document.body.scrollHeight;
      //   scrollHeight > clientHeight
      if (
        scrollHeight > clientHeight &&
        scrollTop + clientHeight === scrollHeight
      ) {
        // 
        const { handleNextPage } = this.props;
        const { page } = this.state;
        handleNextPage(page + 1);
        this.setState({ page: page + 1 });
      }
    });
  }

  render() {
    const { type } = this.props;
    // 5. child2
    console.log(`body got type ${type}`);
    // 6. 
    /**
     * issue
     * 1. 
     * 2. type
     * todo:
     * 1. react ()
     * 2. promise 
     * 3. generator
     */

    /**
     * 
     * 1. 
     * 2. 
     * 3. 
     *   3-1. setState({data})
     *   3-2. data.push('') -> setState({data})
     */
    const list = type.map((item, key) => (
      <div className={styles.item} key={item.id}>
        <h2 style={{ textAlign: "center" }}>#{key + 1}</h2>
        <div style={{ textAlign: "center" }}>
          <img alt="avatar" src={item.owner.avatar_url} width="50%" />
        </div>
        <h4
          style={{
            display: "flex",
            flexDirection: "row",
            height: 36,
            justifyContent: "center",
          }}
        >
          {item.name}
        </h4>
        <div>
          <i
            className="fa fa-user"
            style={{
              display: "inline-flex",
              width: 16,
              justifyContent: "center",
              color: "rgb(255,191,116",
            }}
          />
          {item.name}
        </div>
        <div>
          <i
            className="fa fa-star"
            style={{
              display: "inline-flex",
              width: 16,
              justifyContent: "center",
              color: "rgb(255,215,0",
            }}
          />
          {item.stargazers_count} stars
        </div>
        <div>
          <i
            className="fa fa-code-fork"
            style={{
              display: "inline-flex",
              width: 16,
              justifyContent: "center",
              color: "rgb(129, 195, 245)",
            }}
          />
          {item.forks} forks
        </div>
        <div>
          <i
            className="fa fa-warning"
            style={{
              display: "inline-flex",
              width: 16,
              justifyContent: "center",
              color: "rgb(241, 138, 147)",
            }}
          />
          {item.open_issues} open issues
        </div>
      </div>
    ));

    return (
      <main>
        <div className={styles.appcontent}>{list}</div>
        <div className={styles.spinner}>
          <div className={styles.bounce1} />
          <div className={styles.bounce2} />
          <div className={styles.bounce3} />
        </div>
      </main>
    );
  }
}

export default Body;
